.listContainer{
    display: flex;
    justify-content: center;
    margin-top: 20px;

}

.listWrapper{
    display: flex;
    width: 100%;
    max-width: 1024px;
    gap:20px;
}

.listSearch{
    flex: 1;
    background-color: #febb02;
    padding: 20px;
    border-radius: 10px;
    position: sticky;
    top: 10px;
    height: max-content;
}

.listResult{
    flex: 3;
}

.lsTitle{
    font-size: 20px;
    color:#555;
    margin-bottom: 10px;
}

.lsItem{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
}

.lsItem>input{
    height: 30px;
    padding: 5px;
    border: none;
}

.lsItem>label{
    font-size: 15px;
}

.lsItem>span{
    height: 30px;
    padding: 5px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.lsOptionItem{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    color:#555;
    font-size: 15px;
}

.lsOptionInput{
    width: 50px;
}

.listResult{
    flex:3;
}

.lsOptions{
    padding: 10px;
}

.listSearch>button{
    padding: 10px;
    background-color: #0071c2;
    color:white;
    border: none;
    width: 100%;
    font-weight: 500;
    cursor: pointer;
}

@media (width < 1100px) and (width > 650px){
    .listWrapper{
        max-width: 600px;
        flex-direction: column;
           
    }
    .listSearch{
        position: unset;
    }
}

@media (width < 649px) and (width > 480px){
    .listWrapper{
        /* max-width: 450px; */
        flex-direction: column;
        padding: 0px 10px 0px 10px;   
    }
    .listSearch{
        position: unset;
    }
}

@media (width < 480px){
    .listWrapper{
        /* max-width: 350px; */
        flex-direction: column;
        padding: 20px 10px 100px 10px;
    }
    .listSearch{
        position: unset;
    }
}