.header{
    background-color: #003580;
    color:white;
    display: flex;
    justify-content: center;
    position: relative;
}

.header-container{
    width: 100%;
    max-width: 1024px;
    /* display: flex; */
    padding: 20px 0px 100px 0px;
}

.header-container.listMode{
    width: 100%;
    max-width: 1024px;
    /* display: flex; */
    padding: 20px 0px 0px 0px;
}

.headerList{
    display: flex;
    gap: 20px;
    margin-bottom: 50px;
}

.headerListItem{
    display: flex;
    align-items: center;
    gap:10px;
    cursor: pointer;
}

.headerListItem.active{
    border: 1px solid white;
    border-radius: 20px;
    padding: 10px
}

.headerDesc{
    margin-top: 20px;
}

.headerBtn{
    margin-top: 20px;
    border: none;
    background-color: #0071c2;
    color:white;
    padding:15px;
    font-weight: 500;
    cursor: pointer;
}

.headerSearch{
    height: 50px;
    display: flex;
    border: 3px solid #febb02;
    align-items: center;
    justify-content: space-around;
    background-color: white;
    color:black;
    width: 100%;
    max-width: 1024px;
    position: absolute;
    bottom: -25px;
    border-radius: 5px;
    padding: 10px 0px;
}

.headerSearchItem{
    display: flex;
    align-items: center;
    gap: 10px;
}

.headerInput{
    border: none;
    outline: none;
    padding: 10px 0px;
}

.headerSearchBtn{
    border: none;
    background-color: #0071c2;
    color:white;
    padding:15px;
    font-weight: 500;
    cursor: pointer;
}

.date{
    position: absolute;
    top: 50px;;
    z-index: 2;
}

.options{
    z-index: 2;
    background-color: white;
    color:black;
    position: absolute;
    top:50px;
    border-radius: 5px;
    border: 1px solid #0071c2;
}

.optionsItem{
    display: flex;
    gap: 10px;
    width: 200px;
    justify-content: space-between;
    margin: 10px;
    align-items: center;
}

.optionsCounter{
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 12px;

}

.optionsCounterButton{
    display: flex;
    justify-content: center;
    border: 1px solid #0071c2;
    color:#0071c2;
    background-color: white;
    height: 30px;
    width: 30px;
    border-radius: 5px;
    cursor: pointer;
    align-items: center;
    font-size: 20px;
}

.optionsCounterButton:disabled{
    cursor: not-allowed;
}

.optionsCounterNumber{
    font-size: 20px;
}

.headerSearchItemText{
    cursor: pointer;
}


@media (width < 1100px) and (width > 650px){
    .header-container{
        max-width: 600px;
        padding: 20px 0px 100px 0px;
        
    }
    .header-container.listMode{
        max-width: 600px;
        padding: 10px 0px;
    }
    .headerListItem{
        font-size: 12.5px;
    }
    .headerSearch{
        font-size: 12px;
        max-width: max-content;
    }
}

@media (width < 649px) and (width > 480px){
    .header-container{
        /* max-width: 450px; */
        padding: 20px 10px 100px 10px;
        
    }
    .header-container.listMode{
        padding: 0px 10px;
    }
    .headerListItem{
        font-size: 12.5px;
    }
    .headerSearch{
        font-size: 12.5px;
        flex-direction: column;
        height: 250px;
        position: relative;
    }
    .date{
        position: absolute;
        top: 110px;
        left: 30px;
        z-index: 2;
    }

    .options{
        top:150px
    }
    .headerSearchItem{
        border-bottom: 1px solid black;
    }
    
}


@media (width < 480px){
    .header-container{
        /* max-width: 350px; */
        padding: 20px 10px 100px 10px;
        
    }
    .header-container.listMode{
        padding: 20px 10px 20px 10px;
    }
    .headerListItem{
        font-size: 12.5px;
    }
    .headerList{
        flex-direction: column;
        flex-wrap: wrap;
    }
    .headerSearch{
        font-size: 12.5px;
        flex-direction: column;
        height: 250px;
        position: relative;
    }

    .date{
        position: absolute;
        top: 110px;
        left: 30px;
        z-index: 2;
    }
    .options{
        top:150px
    }
    .headerSearchItem{
        border-bottom: 1px solid black;
    }
}